export function SET_DIET_PLAN(state, dietPlan) {
    let patientName, patientSurname, weekDays, links;
    const dayOfWeek = {
        day: '',
        diet: {},
    };
    weekDays = [];
    var dayObject;

    for (let [key, value] of Object.entries(dietPlan)) {
        switch (key) {
            case 'name':
                patientName = value;
                break;
            case 'surname':
                patientSurname = value;
                break;
            case '_links':
                links = value;
                break;
            default:
                dayObject = Object.create(dayOfWeek);
                dayObject.day = key;
                dayObject.diet = value;
                weekDays.push(dayObject);
                break;
        }
    }

    var dict = [
        {
            key: 1,
            value: 'monday',
        },
        {
            key: 2,
            value: 'tuesday',
        },
        {
            key: 3,
            value: 'wednesday',
        },
        {
            key: 4,
            value: 'thursday',
        },
        {
            key: 5,
            value: 'friday',
        },
        {
            key: 6,
            value: 'saturday',
        },
        {
            key: 7,
            value: 'sunday',
        },
    ];

    weekDays = weekDays.sort(function (a, b) {
        let idx = dict.findIndex((p) => p.value == a.day);
        let aVal = dict[idx].key;
        idx = dict.findIndex((p) => p.value == b.day);
        let bVal = dict[idx].key;

        if (aVal < bVal) {
            return -1;
        }
        if (aVal > bVal) {
            return 1;
        }
        return 0;
    });

    state.patientName = patientName;
    state.patientSurname = patientSurname;
    state.weekDays = weekDays;
    state.links = links;
}
