import Vue from 'vue';
import VueRouter from 'vue-router';
import store from '@/store';
import Home from '../views/Home.vue';
import Login from '../views/Login.vue';
import Welcome from '../views/Welcome.vue';

Vue.use(VueRouter);

const routes = [
    {
        path: '/home',
        name: 'Home',
        component: Home,
        meta: {
            requiresAuth: true,
        },
    },
    {
        path: '/about',
        name: 'About',
        // route level code-splitting
        // this generates a separate chunk (about.[hash].js) for this route
        // which is lazy-loaded when the route is visited.
        component: () =>
            import(/* webpackChunkName: "about" */ '../views/About.vue'),
        meta: {
            requiresAuth: true,
        },
    },
    {
        path: '/',
        name: 'Login',
        component: Login,
    },
    {
        path: '/welcome',
        name: 'Welcome',
        component: Welcome,
    },
];

const router = new VueRouter({
    mode: 'history',
    base: process.env.BASE_URL,
    routes,
});
router.beforeEach((to, from, next) => {
    //console.log('Router: User isLoggedIn? ' + store.getters['moduleUser/isLoggedIn']);

    if (to.matched.some((record) => record.meta.requiresAuth)) {
        if (store.getters['moduleUser/isLoggedIn']) {
            next();
            //console.log('Router: Page requires Auth, User LoggedIn is true');
            return;
        }
        console.log('Router: Page requires Auth, User isLoggedIn is False');
        next('/');
    } else {
        //console.log('Router: Page does not require Auth, calling next()');
        next();
    }
});

export default router;
