import axios from 'axios';

export function login(context, user) {
    return new Promise((resolve, reject) => {
        context.commit('auth_request');
        axios({
            url: 'http://localhost:3001/patient/login',
            data: user,
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
            },
        })
            .then((resp) => {
                const token = resp.data.token;
                localStorage.setItem('token', token);
                // axios.defaults.headers.common['Authorization'] =
                //     'Bearer ' + token;
                axios.defaults.headers.common['Authorization'] = token
                    ? `Bearer ${token}`
                    : '';
                // this.$http.defaults.headers.common['Authorization'] =
                //     'Bearer ' + token;
                console.log('action_login=>user: ' + user);
                context.commit('auth_success', { token, user });
                resolve(resp);
            })
            .catch((err) => {
                context.commit('auth_error');
                localStorage.removeItem('token');

                const { response } = err;
                const { ...errorObject } = response;
                let status = errorObject.status;
                reject(status);
            });
    });
}

export function logout(context, v) {
    return new Promise((resolve) => {
        console.log('inside logout: v is :', v);
        context.commit('logout');
        localStorage.removeItem('token');
        delete axios.defaults.headers.common['Authorization'];
        resolve();
    });
}
